<template>
  <v-card flat color="transparent" max-width="350px">
    <v-card-title>
      <v-layout>
        <v-flex>
          <v-card
            :href="app.link"
            target="_blank"
            elevation="5"
            style="border-radius: 20px"
            width="100px"
            height="100px"
          >
            <v-img :src="logo(app.logo)" />
          </v-card>
        </v-flex>
        <v-flex xs7 mt-9>
          <div  style="color:#D9D6D6">{{app.name}}</div>
        </v-flex>
      </v-layout>
    </v-card-title>
    <v-card-text>
      <div class="body-1" style="color:#D9D6D6">{{app.description}}</div>
      <div class="body-1 mt-2" style="color:#D9D6D6">{{app.tech}}</div>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  
  props: {
    app: {
      type: Object,
      default: function() {
        return {};
      }
    }
  },
  methods: {
    logo(name) {
      return require("../assets/" + name);
    }
  }
};
</script>

